//				props: {
//					oId: 'menuId',
//					label: 'menuName',
//					children: 'children'
//
//				},
				isAdd: false,
				nextTree: [],
				subTree: [],
				delStatus: true,
				input: '',
				statusAll: '',
				sel: '',
				totalRecords: 0 //总条数
					,
				totalPageCnt: 0 //总页数
					,
				pagin_show: true,
