		},
		inject: ['reload'], //provide / inject组合解决--重新加载避免闪烁
		methods: {
			getData(pageIndex, pageSize, status) {
				getRoleListData({
					"pageIndex": pageIndex,
					"pageSize": pageSize,
					'status': status
				}).then(res=> {
					const datas = res.data.data;
					if(Object.keys(datas.dataList).length > 0) {
						this.totalRecords = datas.totalRecords;
						this.totalPageCnt = datas.totalPageCnt;
						this.pageIndex = datas.pageIndex;
						this.tableData = datas.dataList;
						this.pagin_show = true;
					} else {
						this.tableData = [];
						this.pagin_show = false;
					}
				})
			},
			//状态改变
			formatterStatus(row, column) {			
				switch(row.status) {
					case 0:
						return '删除';

						break;
					case 1:
						return '正常';
						break;
					case 2:
						return '禁用';
						break;
				}
			},					
				//点击分页
			updataPage(val) {
				this.pageIndex = val.pageIndex;
				this.pageSize = val.pageSize;
				this.getData(this.pageIndex, this.pageSize,this.statusAll)
			},
			//创建角色 角色权限
			addRole() {
					this.form.name = '',
					this.form.remark = '',
					this.form.departmentList = [],
					this.formJson.oneMenu = [],
					this.isAdd = false;
					this.handleEditShowStatus = false;
					this.formJson.departmentId = [];
				getRoleMenuList().then(res=> {
					
				
						this.dataTree = res.data.data.menuList;
						this.departmentList = res.data.data.departmentList;
						console.log(this.departmentList)
						this.loading = false;					
				
				})
				this.handleEditShow = true;
			},
			
			
			//返回编辑
			backBtn() {
				this.getData(this.pageIndex, this.pageSize, this.statusAll)
				this.handleEditShow = false;
			},
			//角色管理编辑
			handleEdit(index, row) {
				console.log(row)
				this.roleId = row.id;
				this.idx = index;
				this.form = {
					name: row.name,
					remark: row.remark,
					status: row.status
				}
				this.departmentId = [];
				this.handleEditShowStatus = true;	
				checkRole({
					'id':row.id
				}).then(res=>{
					console.log(res)
					if(res.data.code == 1){		
						this.dataTreeCheck = res.data.data.permissionList;
						this.departmentList = res.data.data.departmentList;
						for( let item of this.departmentList){
						if(item.is_check){				
							this.departmentId.push(item.id)
							}
						}
					}else{
						alert(res.data.msg)
					}
				})
				
				this.handleEditShow = true;
				
			},
			loadNode(node, resolve) {

				if(node.level === 1) {
					//点击加载树的下一级调用
					getNextMenuList({
						"id":node.key
					}).then(res => {
						console.log(res)
						if(res.data.code == 1) {
							setTimeout(() => {
								resolve(res.data.data);
							}, 200); //加载下级数据!	
						}

					})
				}
				if(node.level === 2) {
					getNextMenuList({
						"id": node.key
					}).then(res => {

						setTimeout(() => {
							resolve(res.data.data);
						}, 200); //加载下级数据!	
					})
				}
				if(node.level === 3) {
					getNextMenuList({
						"id": node.key
					}).then(res => {

						setTimeout(() => {
							resolve(res.data.data);
						}, 200); //加载下级数据!	
					})
				}
				if(node.level === 4) {
					getNextMenuList({
						"id": node.key
					}).then(res => {

						setTimeout(() => {
							resolve(res.data.data);
						}, 200); //加载下级数据!	
					})
				}
				if(node.level === 5) {
					getNextMenuList({
						"id": node.key
					}).then(res => {

						setTimeout(() => {
							resolve(res.data.data);
						}, 200); //加载下级数据!	
					})
				}
				if(node.level === 6) {
					getNextMenuList({
						"id": node.key
					}).then(res => {

						setTimeout(() => {
							resolve(res.data.data);
						}, 200); //加载下级数据!	
					})
				}
				if(node.level === 7) {
					getNextMenuList({
						"id": node.key
					}).then(res => {

						setTimeout(() => {
							resolve(res.data.data);
						}, 200); //加载下级数据!	
					})
				}
			},


			//创建角色确定
			saveEditRole(){	
				//编辑时
				if(this.handleEditShowStatus){
//					alert(2)
