//						let Json = {};
//						Json.menuId = item.menuId,
//						Json.type = 1,
//						Json.is_check = false, //
//						Json.btns =''; 
//						this.updateRoleJson.oneMenu.push(Json)
//						}
//					}
//					if(item.level == 2){
//						let JsonTwo = {};
//						JsonTwo.menuId = item.menuId,
//						JsonTwo.type = 2,
//						JsonTwo.is_check = false, //
//						JsonTwo.btns =''; 
//						this.updateRoleJson.oneMenu.push(JsonTwo)
//						}
//					}
				
//					this.updateRoleJson.name = this.form.name;
//					this.updateRoleJson.remark = this.form.remark;
//					this.updateRoleJson.departmentId = this.departmentId.toString()						
//					this.updateRoleJson.id = this.roleId;
//					updateRole({"data":JSON.stringify(this.updateRoleJson)})
				}else{
					//创建时
//					this.formJson.oneMenu = [];
//					this.formJson.twoMenu = [];
//						console.log(this.$refs.tree.getCheckedNodes())
//						let checkedArr = this.$refs.tree.getCheckedNodes()
//						for(let item of checkedArr){			
//							if(item.level == 1){
//								let oneMenuJson = {};
//								oneMenuJson.menuId = item.id,
//								oneMenuJson.type = 1,
//								oneMenuJson.is_check = false, //
//								oneMenuJson.btns =''; 
//								oneMenuJson.twoMenu = [];
//								this.formJson.oneMenu.push(oneMenuJson)
//							}
//							if(item.level == 2){
//								let twoMenuJson = {};
//								twoMenuJson.menuId = item.id,
//								twoMenuJson.type = 2,
//								twoMenuJson.btns =''; 
//								twoMenuJson.pId = item.parentId; 
//								this.formJson.twoMenu.push(twoMenuJson)
//								
//							}
//					
