//				}).then(function(res) {
//					console.log(res)
//					t.$message.success('删除成功');
//				});
//
//			}
			handleForOrSta(index, row) {
				this.idDel = row.id
				console.log(this.tableData[index])
				if(row.status == 1){
					this.forOrStaRow(2)
				}else{
					this.forOrStaRow(1)
				}
				
			},
			forOrStaRow(status) {
				forOrStaRole({
					'id': this.idDel,
					'status':status //禁用启用状态
					}).then(res =>{
						console.log(res)
						if(res.data.code == 1) {
							this.getData(this.pageIndex, this.pageSize, this.statusAll)
						} else {
							alert(res.data.msg)
						}
	
					});
				
			},

		}
