</script>

<style lang='scss' scoped="scoped">
	.textTip {
		text-align: center;
	}
	
	.el-tree {
		height: 320px;
		overflow-y: auto;
	}
	
	.dialogHeader {
		width: 490px;
		height: 50px;
		border: 1px solid #000;
		line-height: 50px;
		font-size: 16px;
		background: #f3f2f2;
		border-radius: 5px 5px 0 0;
	}
	
	.dialogBody {
		width: 490px;
		height: 130px;
		text-align: center;
		display: table-cell;
		vertical-align: middle;
	}
	
	.handleSeloShow {
		.el-input {
			width: 438px;
		}
		.adminDetailBox {
			margin-left: 180px;
			padding: 15px 0;
			.addRoleRemark {
				width: 438px;
				height: 120px;
				padding: 6px 6px;
				margin-left: 6px;
				border: 1px solid #ccc;
				border-radius: 3px;
			}
		}
		div {
			.status{
				    margin-left: 12px;
				    font-size: 15px;
			}
			.deptBox{
				line-height: 40px;
				height: 40px;
				.dept{
					width: 80px;
				    text-align: right;
				    float: left;
				    font-size: 14px;
				    color: #606266;
				    line-height: 40px;
				    -webkit-box-sizing: border-box;
				    box-sizing: border-box;
				}
				.deptCheck{
					margin-left: 10px;
				}
			}
			
			
			.product_button {
				margin: 30px 0 20px 50px;
				padding: 0 3px;
				background: #e02222;
				width: 108px;
				height: 36px;
				border-radius: 3px;
				color: #fff;
				font-size: 14px;
				border: none;
				display: inline-block;
				text-align: center;
				line-height: 36px;
				cursor: pointer;
			}
			.adminDetailTitle {
				font-size: 18px;
				margin: 10px 0;
			}
		}
	}
</style>



